import { Button, TextField, Grid, Container } from '@material-ui/core';



import { makeStyles } from '@material-ui/core/styles';
import { Formik } from 'formik';
import firebase from '../firebase/firebaseUtils'

export const SignUp = () => {
	const useStyles = makeStyles({
		wrapper: {
			marginTop: '10rem',
		},
	});
	const styles = useStyles();
	return (
		<Formik
			initialValues={{
				displayName : '',
				email: '',
				password: '',
			}}
			onSubmit={(values, { setSubmitting }) => {
				alert(JSON.stringify(values, null, 2));
				setSubmitting(false);
				

			}}
		>
			{({
				values,
				errors,
				touched,
				handleChange,
				handleBlur,
				handleSubmit,
				isSubmitting,
				/* and other goodies */
			}) => (
				<form onSubmit={handleSubmit}>
					<Container maxWidth='sm'>
						<Grid className={styles.wrapper} container spacing={3}>
							<Grid item xs={12}>
								<TextField
									name = "displayName"
									label='DisplayName'
									type='text'
									autoComplete = "current-displayName"
									variant='outlined'
									fullWidth
									onBlur={handleBlur}
									onChange={handleChange}
									value={values.displayName}
								/>
							</Grid>
							<Grid item xs={12}>
								<TextField
									name = "email"
									label='Email'
									type='email'
									autoComplete = "current-email"
									variant='outlined'
									fullWidth
									onBlur={handleBlur}
									onChange={handleChange}
									value={values.email}
								/>
								{errors.email && touched.email && errors.email}
							</Grid>
							<Grid item xs={12}>
								<TextField
									name = "password"
									label='Password'
									type='password'
									autoComplete = "current-password"
									variant='outlined'
									fullWidth
									onBlur={handleBlur}
									onChange={handleChange}
									value={values.password}
								/>
							</Grid>
							<Grid item xs={12}>
								<Button
									type="submit" disabled={isSubmitting}
									variant='contained'
									color='primary'
									fullWidth
								>
									Submit
								</Button>
							</Grid>
							<Grid item xs={12}>
								<Button
									variant='contained'
									color='primary'
									fullWidth
									
								>
									Google Submit
								</Button>
							</Grid>
						</Grid>
					</Container>
				</form>
			)}
		</Formik>
	);
                
};
